<template>
  <!-- 我的简历详情页 -->
  <div class="myResumeDetial_wrapper">
    <!-- 顶部 -->
    <div class="top">
      <img
        class="return_icon"
        src="../../assets/return_icon.png"
        alt="返回图标"
        @click="returnBtnClkFn"
      >
      <span>简历详情</span>
    </div>
    <!-- 用户信息名片 -->
    <div class="infoCard flex_layout">
      <div class="top">
        <div class="userPic_wrapper">
          <!-- <img class="userPic" src="../../assets/user.png" alt="用户头像">
          <img class="gender_icon manSymbol_icon" src="../../assets/man_symbol.png" alt="男性图标">-->
          <img
            class="userPic"
            v-if="(options.genderString == '男' || options.genderString == undefined || options.genderString == null || options.genderString == '') && (Number(options.bornInteger)<= 24)"
            :src="resumePicOne"
          >
          <img
            class="userPic"
            v-else-if="(options.genderString == '男' || options.genderString == undefined || options.genderString == null || options.genderString == '') && ((Number(options.bornInteger) >= 25 && Number(options.bornInteger) <= 34) || options.bornInteger == undefined || options.bornInteger == null || options.bornInteger == '')"
            :src="resumePicTwo"
          >
          <img
            class="userPic"
            v-else-if="(options.genderString == undefined || options.genderString == null || options.genderString == '') && (options.bornInteger == undefined || options.bornInteger == null || options.bornInteger == '')"
            :src="resumePicTwo"
          >
          <img
            class="userPic"
            v-else-if="(options.genderString == '男' || options.genderString == undefined || options.genderString == null || options.genderString == '') && (Number(options.bornInteger) >= 35 && Number(options.bornInteger) <= 44)"
            :src="resumePicThree"
          >
          <img
            class="userPic"
            v-else-if="(options.genderString == '男' || options.genderString == undefined || options.genderString == null || options.genderString == '') && Number(options.bornInteger) >= 45"
            :src="resumePicFour"
          >
          <img
            class="userPic"
            v-else-if="options.genderString == '女' && ( Number(options.bornInteger)<= 24)"
            :src="resumePicFive"
          >
          <img
            class="userPic"
            v-else-if="options.genderString == '女' && ((Number(options.bornInteger) >= 25 && Number(options.bornInteger) <= 34 )|| options.bornInteger == undefined || options.bornInteger == null || options.bornInteger == '')"
            :src="resumePicSix"
          >
          <img
            class="userPic"
            v-else-if="options.genderString == '女' && (Number(options.bornInteger) >= 35 && Number(options.bornInteger) <= 44)"
            :src="resumePicServen"
          >
          <img
            class="userPic"
            v-else-if="options.genderString == '女' && Number(options.bornInteger) >= 45"
            :src="resumePicEight"
          >
          <img
            v-if="options.genderString == '男'"
            class="gender_icon manSymbol_icon"
            src="../../assets/man_symbol.png"
            alt="男性图标"
          >
          <img
            v-else
            class="gender_icon manSymbol_icon"
            src="../../assets/woman_symbol.png"
            alt="女性图标"
          >
        </div>
        <div class="infos">
          <p class="flex_layout lh-1">
            <span class="name">{{options.name != null ? options.name : '暂无'}}</span>
            <!-- <span
              class="gender_label flex_layout"
            >{{options.genderString != null ? options.genderString : '暂无'}}</span>-->
          </p>
          <p class="lh-1 text-left" style="width:100%">
            <span class="lightBlue_label">{{bornDateToageFn}}</span>
            <span class="lightBlue_label">{{options.education != '' ? options.education : '暂无'}}</span>
            <span
              class="lightBlue_label"
              v-if="(options.workYearInteger!== undefined && options.workYearInteger!== null && options.workYearInteger.length == 0)"
            >暂无</span>
            <span class="lightBlue_label" v-else>{{options.workYearInteger}}</span>
          </p>
        </div>
      </div>
      <div class="bottom">
        <div class="pageView">
          <p class="c-818D8D f-24 text_left">浏览量</p>
          <p class="c-354242 f-30 mt-2 text_left mlf-2">{{options.looked}}次</p>
        </div>
        <div class="noticeView">
          <p class="c-818D8D f-24">关注量</p>
          <p class="c-354242 f-30 mt-2">{{options.downloads}}次</p>
        </div>
        <div class="updateTime">
          <p class="c-818D8D f-24">更新时间</p>
          <p class="c-354242 f-30 mt-5">{{updatesTimeSliceSecond}}</p>
        </div>
      </div>
    </div>
    <!-- 基本信息 -->
    <div class="basicInfo_box">
      <p class="basicInfo_title title f-28">基本信息</p>
      <ul class="infos_list">
        <li>
          <label class="tel_label flex_layout w-72">
            <span>电&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;话</span>
          </label>
          <span>
            <a
              v-if="options.mobile != 'NA'"
              class="blue fw-550"
              :href="'tel:' + options.mobile"
            >{{options.mobile}}</a>
            <!-- <a v-if="options.mobile != 'NA'" class="blue fw-550"  href="javascript:avoid(0)" ref="tel">{{options.mobile}}</a>   -->
            <span v-else class="blue fw-550" @click="lookCtMethodsClk">查看联系方式</span>
          </span>
        </li>

        <li>
          <label class="w-72">籍贯城市</label>
          <span>{{nativePlaceFn}}</span>
        </li>

        <li>
          <label class="w-72">技能标签</label>
          <div class="w-270">
            <span
              v-if="(options.skillTags!== undefined && options.skillTags!== null && options.skillTags.length == 0)"
              class="basicInfo-span_style"
            >暂无</span>
            <span
              v-for="(item, index) in options.skillTags"
              :key="index"
              class="basicInfo-span_style"
            >{{item}}</span>
          </div>
        </li>
        <!-- <li>
          <label class="born-label">出生年月</label>
          <span class="born-span">{{options.born != '' ? options.born : '暂无'}}</span>
        </li>-->
        <!-- <li>
          <label>最高学历</label>
          <span>{{options.education != '' ? options.education : '暂无'}}</span>
        </li>-->
        <!-- <li>
          <label>工作年限</label>
          <span
            v-if="(options.workYearInteger!== undefined && options.workYearInteger!== null && options.workYearInteger.length == 0)"
          >暂无</span>
          <span v-else>{{options.workYearInteger}}</span>
        </li>-->
        <li>
          <label class="w-72">意向岗位</label>
          <div class="w-270">
            <span
              v-if="(options.intentionPosition !== undefined && options.intentionPosition !== null && options.intentionPosition == 0)"
              class="basicInfo-span_style"
            >暂无</span>
            <span
              v-for="(item, index) in options.intentionPosition"
              :key="index"  class="basicInfo-span_style"
            >{{item.positionName}}</span>
          </div>
        </li>

        <li class="intention-city">
          <span class="label">意向城市</span>
          <div class="w-270">
            <span
              v-if="(options.intentionalCity !== undefined && options.intentionalCity !== null && options.intentionalCity == 0)"
              class="basicInfo-span_style"
            >暂无</span>
            <span
              v-else
              class="wantCity wantCity-span basicInfo-span_style"
              v-for="(item, index) in options.intentionalCity"
              :key="index"
            >
              <span>{{item.intentionalPlace}}</span>
            </span>
          </div>
        </li>

        <div class="clear"></div>
      </ul>
    </div>
    <!-- 投递职位信息 -->
    <div class="deliverPositionInfo_box">
      <p class="basicInfo_title title f-28">工作经历</p>
      <ul class="dpi_list" v-if="experienceList.length > 0">
        <li v-for="(item, index) in experienceList" :key="index">
          <p
            class="c-818D8D f-26"
          >工作时间:{{item.startYear}}{{item.startMonth}}-{{item.endYear}}{{item.endMonth}}</p>
          <p class="c-0B231F f-26 mt-19">{{item.company}}</p>
        </li>
      </ul>
      <div class="blank_page noWorkExprienceData" v-else>
        <img src="../../assets/deliver_blank.png" alt="投递空图片">
        <p>暂无工作经历</p>
      </div>
    </div>
    <!-- mask -->
    <div class="mask" v-show="maskShow" @click="maskPersonalClk"></div>
    <!-- 人才搜索积分扣除modal -->
    <div class="score_modal" v-show="jMarkModalShow">
      <p class="f-28 c-12221F mb-2 lh-1 mb-10">您好,本次操作需要50个积分点,</p>
      <p class="f-28 c-12221F lh-1">操作成功后相关简历将进入您的简历库?</p>
      <mt-checklist v-model="value" :options="option" @change="checkon"></mt-checklist>
      <div class="modal_footerBtn">
        <div class="btn" @click="jModalCancelBtnClk">取消</div>
        <div class="btn" @click="jModalSureBtnClk">确定</div>
      </div>
    </div>
    <!-- 积分不足modal -->
    <div class="score_modal" v-show="showIntegration">
      <p class="f-28 c-12221F mb-2 lh-1 mb-10" style="margin-top:1.9375rem">您好,您的积分不足,请先购买积分</p>
      <div class="modal_footerBtn">
        <div class="btn" @click="falseIntegration">我在想想</div>
        <div class="btn" @click="trueIntegration">去购买</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Util from "@/js/mUtils";
import { Checklist } from "mint-ui"; // mint-ui CheckList
Vue.component(Checklist.name, Checklist);
import { Indicator } from 'mint-ui'; // mint-ui 加载中提示
import { Notify } from "vant"; // toast提示
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
export default {
  data() {
    return {
      globalApiUrl: domain.testUrl, // 全局域名
      qdsGlobalApiUrl: domain.qdsDomain, // 全局域名
      searchGlobalApiUrl: domain.searchTestUrl, // 人才搜索全局域名
      resumeListData: "", // 列表数据
      currentResumeId: "", // 当前简历id
      currentResumeIndex: "", // 当前简历在列表中的位置
      options: { nativePlace: "" }, // 详情页面数据
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      experienceList: [], // 工作经历
      updateTimeArr: [], // 我的简历更新时间数组
      pageId: "", // 不同页面来源标记
      certificationStatus: "", // 存储获取的认证状态
      maskShow: false, // maks默认隐藏
      jMarkModalShow: false, // 积分扣除modal默认隐藏
      showIntegration: false, // 积分不足modal默认隐藏
      value: [""], // mint-ui checkList  值默认为空,不选中状态。不要赋值否则默认为选中状态
      option: [
        {
          label: "知道啦，以后不用提示了哦",
          value: "A"
        }
      ],
      idArr: [], // 预存每个简历的id
      resumeId: "",
      resumeIndex: "",
      getUserStorageId:'',  // 存储获取storage的用户ID
      changeFlag: true, // 积分扣除modal的单选框点击标记
    };
  },
  methods: {
    // 知道啦以后不用提示了哦change事件 用户选中checkBox登陆后禁止显示积分扣除modal
    checkon(){ 
      this.changeFlag = !this.changeFlag; // 点击切换标记
      if(this.changeFlag == false){  // 已选中
          this.value = ["A"];        
      }
    },
    beforeRouteLeave(to, from, next) {
      // 返回列表页面保留滚动条高度
      // 设置下一个路由的 meta
      to.meta.keepAlive = true; // 让列表页面缓存，即不刷新
      next();
    },
    updated() {
      this.$nextTick(function() {
        let position = this.$store.state.position;
        window.scroll(0, position);
      });
    },
    // 左上角按钮点击事件
    returnBtnClkFn() {
      this.$router.push("/qdsSearchResume"); // 返回投递记录列表页
      if (this.pageId == 3) {
        this.$router.push({ path: "/qdsSearchResume", query: { id: 3 } }); // 返回投递记录列表页
      }else if(this.pageId == 1){
        this.$router.push({ path: "/qdsSearchResume", query: { id: 1 } }); // 返回我的简历列表页
      }
    },
    init() {
      var that = this;
      var id = that.currentResumeId;
      that.resumeListData.forEach((item, index) => {
        // index 从 0 开始计数
        if (item.id == id) {
          that.options = item; // 遍历后数据赋值options(页面数据遍历来源)
          that.currentResumeIndex = index;
        }
      });
    },
    // 查看联系方式点击事件
    lookCtMethodsClk() {
      this.getUserStorageId = localStorage.getItem("qdsSCorpUserId"); // 获取本地存储当前用户ID          
      this.certificationStatus = sessionStorage.getItem(
        "qdsMCertificationStatus"
      ); // 获取认证状态 1未认证  2认证中  3成功  4失败
      if (this.certificationStatus == 1) {
        // 未认证跳转认证信息填写页面
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 2) {
        // 认证中 跳转至“企业认证-等待审核”页面 OK
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 3) {
        // 认证成功 显示消耗积分提示(若用户已勾选不再显示,则不再显示积分扣除modal)         

        // 判断userCheckBoxShwOrNt(值为用户ID)中是否有当前用户ID,有-不再显示积分扣除modal
        var cutDownJMarkFlag = localStorage.getItem('userCheckBoxShwOrNt'); // 取出当前用户ID 
                   
        if(cutDownJMarkFlag == this.getUserStorageId){  // this.getUserStorageId 获取联系方式按钮点击获取到的用户ID                  
          this.jMarkModalShow = false; // 不显示积分消耗提示modal
          this.maskShow = false; // 不显示mask
          this.jModalSureBtnClk(); // 扣除积分modal不显示,调用扣除modal上的点击事件
        }else{                   
          this.jMarkModalShow = true; // 显示积分消耗提示modal
          this.maskShow = true; // 显示mask
        }

      } else if (this.certificationStatus == 4) {
        // 认证失败 跳转至认证页面
        this.$router.push("/qdsAttestation");
      }
    },
    // mask点击事件
    maskPersonalClk() {
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.maskShow = false; // mask隐藏
      this.showIntegration = false; // 关闭积分不足modal
      this.value = [""];  // mask点击移除勾选的不再显示积分扣除
    },
    // 积分扣除modal的取消事件
    jModalCancelBtnClk() {
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.maskShow = false; // mask隐藏
      this.value = [""];  // 积分扣除modal点击取消,移除选中的不在显示单选框
    },
    // 积分扣除modal的确定事件
    jModalSureBtnClk() {      
      // loading
        Indicator.open({
          text: '请求中...',
          spinnerType: 'snake'
        });

        this.jMarkModalShow = false; // 关闭积分消耗提示modal
        this.maskShow = false; // mask隐藏

      
      // 用户已勾选单选框 不再显示积分扣除modal
      if(this.value[0] == 'A'){        
        // 选中状态
        localStorage.setItem("userCheckBoxShwOrNt", this.getUserStorageId); // userId存storage,积分消耗modal是否显示依据        
      }

      // 购买简历
      var that = this;
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      that.resumeId = localStorage.getItem("telentSearchListId"); // 当前简历ID
      that.idArr.forEach((item, index) => {
        // 放置有多个id的情况
        if (that.resumeId == item) {
          that.resumeIndex = index;
        }
      });
      that
        .axios(
          that.qdsGlobalApiUrl + "channelResume/followResume?token=" + token,
          {
            method: "post",
            params: {
              userId: userId,
              resumeId: that.resumeId,
              source: "search"
            },
            timeout: 10000
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: "简历获取成功，请到“我的简历”查看",
              duration: 1500,
              background: "#20D3B3"
            });
            // 获取接口返回电话姓名赋值页面
            that.options.mobile = response.data.data.mobile;
            that.options.name = response.data.data.name;

            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.maskShow = false; // 关闭mask
          } else if (response.data.code == 30002) {
             Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: "积分不足",
              duration: 1500,
              background: "#FF6262"
            });
            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.showIntegration = true; // 显示积分不足modal
            that.maskShow = true; // 显示mask
          } else {
             Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: response.data.message,
              duration: 1500,
              background: "#FF6262"
            });
            that.showIntegration = false; // 关闭积分不足modal
            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.maskShow = false; // 关闭mask
          }
        })
        .catch(function(error) {
          Indicator.close(); // 数据请求成功隐藏loading
           Notify({
              message: '请求失败',
              duration: 1500,
              background: '#FF6262'
          });
          that.jMarkModalShow = false; // 关闭积分消耗提示modal
          that.maskShow = false; // 关闭mask
          that.showIntegration = false; // 关闭积分不足modal
        });
    },
    // 积分不足modal的我在想想
    falseIntegration() {
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.maskShow = false; // mask隐藏
      this.showIntegration = false; // 关闭积分不足modal
    },
    // 积分不足modal 充值按钮
    trueIntegration() {
      this.showIntegration = false; // 关闭积分不足modal
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.maskShow = false; // mask隐藏
      this.$router.push("/qdsPersonal"); // 跳转到充值页面
    }
  },
  mounted() {
    this.pageId = this.$route.query.id; // 获取url的id
    if (this.pageId == 1) {
      // 页面来源是我的简历列表myResumeList
      var that = this;
      var qdsResumeList = localStorage.getItem("qdsResumeList");
      that.resumeListData = JSON.parse(qdsResumeList); // 把字符串转换成JSON对象
      that.currentResumeId = localStorage.getItem("qdsResumeId"); // 当前点击的简历id  会更改, 每次更改都要存到localStorage中
      this.init(); // 首次页面数据的处理  或 刷新后的页面数据处理
      // 列表数据遍历得到我的简历更新时间以及对应ID
      that.resumeListData.forEach((item, index) => {
        var updateTimeObj = new Object();
        updateTimeObj.id = item.id;
        updateTimeObj.updateTime = item.updateTime;
        updateTimeObj.nationalPlace = this.nativePlaceFn;
        that.updateTimeArr.push(updateTimeObj);
      });

      // 我的简历id和更新时间存storage, deliverRecordDetial页面获取并使用该数据
      var updateTimeArrString = JSON.stringify(that.updateTimeArr); //  将JSON对象转化成字符串
      localStorage.setItem("updateTimeArrStorage", updateTimeArrString); // 列表数据
    }
  },
  created() {
    this.pageId = this.$route.query.id; // 截取url中的id参数，判断页面来源
    if (this.pageId == 3) {
      // 页面来源 人才搜索列表页telentSearchList, myResumeDetail此页面也用作人才搜索列表详情页
      var that = this;
      var telentSearchList = localStorage.getItem("telentSearchList"); // 取出telentSearchList页面存入storage的列表数据
      that.resumeListData = JSON.parse(telentSearchList); // 把字符串转换成JSON对象
      that.currentResumeId = localStorage.getItem("telentSearchListId"); // 当前点击的简历id会更改,每次更改都要存到localStorage中
      this.init(); // 首次页面数据的处理或刷新后页面数据处理
    }
  },
  computed: {
    // 籍贯字符串截取
    nativePlaceFn() {
      if (
        this.options.nativePlace == "" ||
        this.options.nativePlace.indexOf("null") == 0
      ) {
        return "暂无";
      } else {
        return this.options.nativePlace.substring(
          0,
          this.options.nativePlace.indexOf("null")
        );
      }
    },
    // 更新时间截取秒
    updatesTimeSliceSecond() {
      if (
        this.options.updateTime != null &&
        this.options.updateTime != undefined &&
        this.options.updateTime.length > 0
      ) {
        return this.options.updateTime.substring(
          0,
          this.options.updateTime.length - 3
        );
      } else {
        return "暂无";
      }
    },
    // 出生年月转换年龄
    bornDateToageFn() {
      if (
        this.options.born != "" &&
        this.options.born != null &&
        this.options.born != undefined
      ) {
        let bornDateYear = this.options.born.substring(0, 4); // this.options.born  出生时间
        let date = new Date();
        let nowDateYear = date.getFullYear(); // 当前年份
        let yearResult = nowDateYear - bornDateYear;
        return yearResult + "岁";
      } else {
        return "暂无";
      }
    }
  },
  components: {
    Notify // toast提示
  }
};
</script>

